.background {
  position: fixed;
  z-index: 99;
  top:0;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background:#4b415c;
  opacity: 0.8;
}

form {
  position:fixed;
  top:50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 999;
  width: 70%;
  height: 40vw;
  border-radius: 5%;
  box-shadow: 1px 2px 5px 1px #4c8196;
  background: #FFF;
}

form > input {
  background: none;
  outline: none;
  border: none;
  border-bottom: 1px solid #4c8196;

  padding: 0.5rem;
  font-size: 1.125rem;
  line-height: 1.5;
}

form > button {
  padding-top: 2.5vw;
  background: none;
  outline: none;
  border: none;
  color: #4c8196;
  padding-left:1rem;
  padding-right:1rem;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: 0.1s background ease-in;
}

.rewrite > svg {
  padding-top: 20px;
  padding-left: 1rem;
  padding-right: 1rem;
  color: #4c8196;
  font-size: 1.5rem;
}